.outerContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #1A1A1D;
  }
  
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    height: 70%;
    width: 70%;
    border-radius: 10px;
  }
  
  @media screen and (max-width: 768px){
    .container {
    
      background: #1A1A1D;;
      height: 90%;
      width: 100%;

    }


  }