.infoBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #2979FF;
    border-radius: 10px;
    height: 60px;
    width: 100%;

  }
  
  .leftInnerContainer {
    flex: 0.5;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: white;
  }
  
  .rightInnerContainer {
    display: flex;
    flex: 0.5;
    justify-content: flex-end;
    margin-right: 5%;
  }
  
  .onlineIcon {
    margin-right: 5%;
    padding: 20px;
    border-radius: 20px;
    background-color:rgb(65, 235, 59);
  }

.close_button{
font-size: 3rem;
text-decoration: none;
color:white;
background-color:red;
padding: 2px;
padding-left: 15px;
padding-right: 15px;

}
  
