html, body {
    font-family: 'Roboto', sans-serif;
    padding: 0;
    margin: 0;
  }
  
  #root {
    height: 100vh;
  }
  
  * {
    box-sizing: border-box;
  }
  
  .joinOuterContainer {
    display: flex;
    justify-content: center;
    text-align: center;
    height: 100vh;
    align-items: center;
    background-color: #1A1A1D;
  
  }
  
  .joinInnerContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 20%;
    background-color:#1A1A1D;;
    height: 50%;
    border-radius:25px;
  }

  .JoinInput {
    border-radius: 15px;
    padding: 15px 20px;
    width: 90%;
    font-size: 1.5rem;
  }
  
  .heading {
    color: white;
    font-size: 2.5em;
    padding-bottom: 10px;
    border-bottom: 2px solid white;
    font-style: italic;
    
  }
  
  .button {
      color: #fff !important;
      text-transform: uppercase;
      text-decoration: none;
      background: #2979FF;
      padding: 20px;
      border-radius: 15px;
      display: inline-block;
      border: none;
      width: 80%;
    }    



.mt-20 {

    margin-top: 20px;

  }
  
  @media (min-width: 320px) and (max-width: 480px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 90%;
    }
  
  
  }
  
  @media (min-width: 480px) and (max-width: 600px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 80%;
    }
  }
  @media (min-width: 600px) and (max-width: 1000px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 60%;
    }
  
  
  }
  @media (min-width: 1000px) and (max-width: 1500px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width: 50%;
    }
  
  
  }
  @media (max-width: 330px) {
    .joinOuterContainer {
      height: 100%;
    }
  
    .joinInnerContainer {
      width:90%;
      height: 100%;
    }
  
  
  }
  
  button:focus {
    outline: 0;
  }